.Accolades {
  width: 100%;
  height: 280%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(./accolades_background.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  }
  .superbowl {
    position: relative;
    pointer-events: none;
    user-select: none;
    top: 180px;
    left: 40px;
    width:100%;
  }
  .superbowl-image {
    height: 280px;
    width: 280px;
    pointer-events: none;
    user-select: none;
    display: inline-block;
    position: relative;
    top: 40px;
    left: -100px;
  }
  .superbowl-text {
    height: 300px;
    width: 700px;
    max-width: 700px;
    font-family: Nanum Gothic;
    font-size: 13pt;
    line-height: 27pt;
    letter-spacing: .1pt;
    text-align: center;
    display: inline-block;
  }
  .superbowl-header {
    font-family: Artifika;
    font-size: 25pt;
    color: #012F88;
    position: relative;
    top: 110px;
    left: 500px;
    width: 700px;
  }
