.Contact {
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(./contact_background.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
  }
  .pinwheel-parent {
    position: relative;
    top: 190px;
    left: -290px;
    z-index: -1;
  }
  .Contact-logo {
    width: 100px;
    height: 100px;
    position: relative;
  }
  .pinwheel {
    width: 10px;
    height: 10px;
    position: relative;
    top: -45px;
    left: -55px;
  }
  .stick {
    position: relative;
    top: 215px;
    left: 98px;
  }
  .email {
    font-family: Assistant;    
    letter-spacing: 3pt;
    font-size: 25pt;
    color: #114895;
    position: relative;
    top:90px;
  }